.widget{
    background: $light-blue;
    margin-bottom: $xl-size;
}

.widget-header{
    background: $blue;
    color:$off-white;
    padding: $s-size;
    display: flex;
    justify-content: space-between;
    

}
.widget-header__title{
    margin: 0
}

.widget__message{
    color:$off-white;
    text-align: center;
    margin: 0;
    padding: $l-size;
    border-bottom: 1px solid lighten($light-blue,10%);
}